import mSearch from '@/components/mehaotian-search/mehaotian-search.vue';
export default {
name: "",
components: {
  mSearch
},
props: {},
data () {
  return {
	  keywords:'',
	  page:1,
	  size:30,
	  search:[]

    }
	
  },
  methods: {
	goto(e) {
	            this.keywords=e
				this.getSearch()
	          },
	goDetails(e){
	
		uni.navigateTo({
			url:`../details/details?id=${e}`
		})
	},      
    getSearch(){
		uni.request({
			url:`https://www.imovietrailer.com/superhero/search/list?keywords=${this.keywords}&qq=2684425594&page=${this.page}&pageSize=${this.size}`,
			method:'POST',
			success: (res) => {
			    this.search=res.data.data.rows
				console.log(res);
				
			}
		})
	},
	
  },
  mounted () {
    this.getSearch()
  },
  onLoad () {

  },
  filters: {

  },
  computed: {

  },
  watch: {

  },
  directives: {

  }
}
